.navbar {
    position        : fixed;
    background-color: var(--bg-primary);
    transition      : width 600ms ease;
    overflow        : hidden;
}

.navbar-nav {
    list-style    : none;
    padding       : 0;
    margin        : 0;
    display       : flex;
    flex-direction: column;
    align-items   : center;
    height        : 100%;
}

.nav-item {
    width       : 100%;
    padding-top: 1em;
}

.nav-link {
    display        : flex;
    height         : 4rem;
    color          : var(--text-primary);
    align-items    : center;
    letter-spacing : 0.3ch;
    text-decoration: none;
    filter         : grayscale(100%) opacity(0.7);
    transition     : var(--transition-speed);
}

.nav-link:hover {
    filter    : grayscale(0%) opacity(1);
    background: var(--bg-secondary);
    color     : var(--text-secondary);
}

.link-text {
    display    : none;
    margin-left: 1rem;
}

.nav-link svg {
    width    : 2rem;
    min-width: 2rem;
    margin   : 0 1.5rem;
}

.fa-primary {
    color: var(--special-primary);
}

.fa-secondary {
    color: var(--special-secondary);
}

.fa-primary,
.fa-secondary {
    transition: var(--transition-speed);
}

.logo {
    font-weight   : bold;
    text-transform: uppercase;
    margin-bottom : 1rem;
    text-align    : center;
    color         : var(--text-secondary);
    background    : var(--bg-secondary);
    font-size     : 1.5rem;
    letter-spacing: 0.3ch;
    width         : 100%;
}

.logo svg {
    transform : rotate(0deg);
    transition: var(--transition-speed);
}

.logo-text {
    display   : inline;
    position  : absolute;
    left      : -300px;
    transition: var(--transition-speed);
}

.navbar:hover .logo svg {
    transform: rotate(-360deg);
}

/* Small screens */
@media only screen and (max-width: 600px) {
    .navbar {
        bottom: 0;
        width : 100vw;
        height: 5rem;
        display: flex;
        z-index: 2;
        flex-direction: row;
        justify-content: center;
    }


    .logo {
        margin-bottom : -0.7em;
        color         : var(--text-secondary);
        background    : none;
    }

    .navbar-nav {
        flex-direction: row;
        display: flex;
        gap: 0.5em;
    }


    .link-text {
        display    : none;
    }
}

/* Large screens */
@media only screen and (min-width: 600px) {
    .navbar {
        top   : 0;
        width : 5rem;
        height: 100vh;
    }

    .navbar:hover {
        width: 16rem;
    }

    .navbar:hover .link-text {
        display: inline;
    }

    .navbar:hover .logo svg {
        margin-left: 12rem;
    }

    .navbar:hover .logo-text {
        left: 0px;
    }
}